@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{height:100px;background-color:#FE7606}.navbar .navigation{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.navbar .navigation img{height:85px;width:120px;margin-left:50px}.navbar .navigation a{color:#000;transition:0.5s}.navbar .navigation a:hover{color:#fff}.menu-bars{margin-right:3rem;margin-top:10px;font-size:2rem;background:none;color:#000;transition:0.5s}.menu-bars:hover{color:#fff}.nav-menu{background:#FE7606;width:200px;height:100vh;display:-webkit-flex;display:flex;position:fixed;top:0;right:-100%;transition:1.5s}.nav-menu.active{right:0;transition:1.5s}.nav-text{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;padding:8px 0px 8px 0px;list-style:none;height:30px}.nav-text a{text-decoration:none;color:#000;font-size:18px;font-weight:bold;width:95%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:0 16px;border-radius:4px;transition:0.5s}.nav-text a:hover{text-decoration:underline;color:#fff}.nav-items{width:100%}.navbar-toggle{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;width:100%;height:80px}#burger-menu{color:#fff;transition:0.5s}#burger-menu:hover{text-decoration:underline;color:#d4af37}.span-item{margin-left:16px}.home{background-image:url(/homemade-restaurant/static/media/home-bg.07567954.jpg);background-repeat:no-repeat;background-size:cover;background-position-x:center;background-position-y:top;height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start}.home .home-elements{margin-top:15%;margin-left:5%;color:#fff}.home .home-elements h1{text-transform:uppercase;font-size:80px;font-family:'Roboto Slab', serif;margin:0;text-shadow:4px 4px #000}.home .home-elements p{width:440px;font-size:17px}.home .home-elements p span{color:#FE7606;font-weight:bold;cursor:pointer}.home .home-elements p span:hover{text-decoration:underline}.home .home-elements button{background-color:transparent;color:#FE7606;border:none;outline:none;border-bottom:2px solid #FE7606;font-weight:bold;font-size:16px;text-transform:uppercase;cursor:pointer;transition:0.5s}.home .home-elements button:hover{color:#fff;border-bottom:2px solid #fff}.menu{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center}.menu img{height:430px;width:320px;border:5px solid #FE7606;padding:5px}.menu .meat-img{margin-top:7%}.menu .menu-elements{background-color:#FE7606;padding:30px 20px}.menu .menu-elements h1{font-size:84px;margin:0}.menu .menu-elements p{width:300px;font-weight:bold;line-height:34px}.menu .menu-elements button{background-color:transparent;color:#000;border:none;cursor:pointer;font-size:20px;font-weight:bold;transition:0.5s}.menu .menu-elements button:hover{color:#fff;text-decoration:underline}@media only screen and (max-width: 900px){.home{height:60vh}.home .home-elements h1{font-size:44px}.home .home-elements p{width:270px}.home .home-elements button{font-size:20px}}

